import Vue from 'vue'
import Vuex from 'vuex'
import Products from '../../public/products.json'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    allProducts: []
  },
  mutations: {
    load (state) {
      state.allProducts = Products
    }
  },
  actions: {
    getProducts (ctx) {
      ctx.commit('load')
    }
  },
  getters: {
    returnState (state) {
      return state.allProducts
    }
  }

})
