<template>
  <div class="home">
    <Header />
    <h1>Продуктовая витрина с фильтрацией уровня риска</h1>
    <Selects @change="change"/>
    <Cards :currentSelect='currentSelect' @length='lengthChange' :filteredItems='filteredItems'/>
    <Pagination :length='length' @click="currentFilter"/>
  </div>
</template>

<script>
import Cards from '@/components/Cards'
import Selects from '@/components/Selects.vue'
import Header from '@/components/Header.vue'
import Pagination from '@/components/Pagination.vue'

export default {
  name: 'home',
  data () {
    return {
      currentSelect: null,
      length: null,
      filteredItems: null
    }
  },
  components: {
    Selects,
    Header,
    Cards,
    Pagination
  },
  methods: {
    change (e) {
      this.currentSelect = e
    },
    lengthChange (e) {
      this.length = e
    },
    currentFilter (e) {
      this.filteredItems = e
    }
  },
  mounted () {
    this.$store.dispatch('getProducts')
  }
}
</script>

<style lang="scss" scoped>
  h1{
    text-align: center;
  }
</style>
