<template>
  <el-pagination
  :page-size="6"
  layout="prev, pager, next"
  @current-change='click'
  :total="length">
</el-pagination>
</template>

<script>
export default {
  props: ['length'],
  methods: {
    click (ev) {
      this.$emit('click', ev * 6)
    }
  },
  mounted () {
    this.$emit('click', 6)
  }
}
</script>

<style lang="scss" scoped>
 .el-pagination {
   display: flex;
  justify-content: center;
 }
</style>
